.van-notify {
  text-align: center;
  word-wrap: break-word;
  padding: 0.256rem 0.64rem;
  padding: var(--notify-padding, 0.256rem 0.64rem);
  font-size: 0.59733rem;
  font-size: var(--notify-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--notify-line-height, 0.85333rem);
}
.van-notify__container {
  position: fixed;
  top: 0;
  left: 0;
  box-sizing: border-box;
  width: 100%;
}
.van-notify--primary {
  background-color: #1989fa;
  background-color: var(--notify-primary-background-color, #1989fa);
}
.van-notify--success {
  background-color: #07c160;
  background-color: var(--notify-success-background-color, #07c160);
}
.van-notify--danger {
  background-color: #ee0a24;
  background-color: var(--notify-danger-background-color, #ee0a24);
}
.van-notify--warning {
  background-color: #ff976a;
  background-color: var(--notify-warning-background-color, #ff976a);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
