.van-sidebar-item {
  display: block;
  box-sizing: border-box;
  overflow: hidden;
  border-left: 0.128rem solid transparent;
  -webkit-user-select: none;
          user-select: none;
  padding: 0.85333rem 0.512rem 0.85333rem 0.34133rem;
  padding: var(--sidebar-padding, 0.85333rem 0.512rem 0.85333rem 0.34133rem);
  font-size: 0.59733rem;
  font-size: var(--sidebar-font-size, 0.59733rem);
  line-height: 0.85333rem;
  line-height: var(--sidebar-line-height, 0.85333rem);
  color: #323233;
  color: var(--sidebar-text-color, #323233);
  background-color: #f7f8fa;
  background-color: var(--sidebar-background-color, #f7f8fa);
}
.van-sidebar-item__text {
  position: relative;
  display: inline-block;
  word-break: break-all;
}
.van-sidebar-item--hover:not(.van-sidebar-item--disabled) {
  background-color: #f2f3f5;
  background-color: var(--sidebar-active-color, #f2f3f5);
}
.van-sidebar-item::after {
  border-bottom-width: 0.04267rem;
}
.van-sidebar-item--selected {
  color: #323233;
  color: var(--sidebar-selected-text-color, #323233);
  font-weight: 500;
  font-weight: var(--sidebar-selected-font-weight, 500);
  border-color: #ee0a24;
  border-color: var(--sidebar-selected-border-color, #ee0a24);
}
.van-sidebar-item--selected::after {
  border-right-width: 0.04267rem;
}
.van-sidebar-item--selected,
.van-sidebar-item--selected.van-sidebar-item--hover {
  background-color: #fff;
  background-color: var(--sidebar-selected-background-color, #fff);
}
.van-sidebar-item--disabled {
  color: #c8c9cc;
  color: var(--sidebar-disabled-text-color, #c8c9cc);
}
/* stylelint-disable selector-type-no-unknown, selector-max-type */
/**
 * Entry of basic styles
 */
.van-ellipsis {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.van-multi-ellipsis--l2 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-multi-ellipsis--l3 {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-line-clamp: 3;
  /* autoprefixer: ignore next */
  -webkit-box-orient: vertical;
}
.van-clearfix::after {
  display: table;
  clear: both;
  content: '';
}
.van-hairline,
.van-hairline--top,
.van-hairline--left,
.van-hairline--right,
.van-hairline--bottom,
.van-hairline--top-bottom,
.van-hairline--surround {
  position: relative;
}
.van-hairline::after,
.van-hairline--top::after,
.van-hairline--left::after,
.van-hairline--right::after,
.van-hairline--bottom::after,
.van-hairline--top-bottom::after,
.van-hairline--surround::after {
  position: absolute;
  box-sizing: border-box;
  -webkit-transform-origin: center;
          transform-origin: center;
  /* cover wechat button:after default transforn-origin */
  content: ' ';
  pointer-events: none;
  top: -50%;
  right: -50%;
  bottom: -50%;
  left: -50%;
  border: 0 solid #ebedf0;
  -webkit-transform: scale(0.5);
          transform: scale(0.5);
}
.van-hairline--top::after {
  /* prettier-ignore */
  border-top-width: 1PX;
}
.van-hairline--left::after {
  /* prettier-ignore */
  border-left-width: 1PX;
}
.van-hairline--right::after {
  /* prettier-ignore */
  border-right-width: 1PX;
}
.van-hairline--bottom::after {
  /* prettier-ignore */
  border-bottom-width: 1PX;
}
.van-hairline--top-bottom::after {
  /* prettier-ignore */
  border-width: 1PX 0;
}
.van-hairline--surround::after {
  /* prettier-ignore */
  border-width: 1PX;
}
html,
body,
page {
  font-size: 0.59733rem;
}
